.genai-logo {
    position: fixed;
    bottom: 20px;
    right: 20px;
    width: 60px;
    height: 60px;
    border-radius: 5%;
    cursor: pointer;
    background-color:blue;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .genai-logo img {
    width: 100%;
    height: 100%;
    border-radius: 10%;
  }
  